.webmain .con .title h2 {
  background: url("../../../images/sprite_video.gif") 0 0 no-repeat; }

.webmain .video {
  position: relative; }
  .webmain .video .title h2 {
    width: 145px;
    background-position: 0 -120px; }
  .webmain .video .title .group li {
    width: 90px; }
  .webmain .video .pagelist {
    bottom: -45px; }
  .webmain .video .inner {
    width: 1230px;
    margin-left: -30px; }
    .webmain .video .inner .item {
      float: left;
      margin-left: 30px;
      margin-bottom: 30px; }
      .webmain .video .inner .item img, .webmain .video .inner .item sub {
        width: 380px;
        height: 285px; }
      .webmain .video .inner .item .text {
        position: absolute;
        width: 380px;
        height: 60px;
        margin-top: -60px;
        color: #fff;
        text-indent: 20px;
        background: url("../../../images/space.png") 0 0 repeat;
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000,endColorstr=#99000000);
        background: rgba(0, 0, 0, 0.6); }
        .webmain .video .inner .item .text h1 {
          font-size: 14px;
          line-height: 30px; }
        .webmain .video .inner .item .text h3 {
          font-size: 12px;
          line-height: 30px; }
      .webmain .video .inner .item sub {
        position: absolute;
        margin-top: -285px;
        background: transparent;
        background: url("../../../images/space.png") 0 0 repeat; }
        .webmain .video .inner .item sub:hover {
          background: rgba(0, 0, 0, 0.3);
          filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#4c000000,endColorstr=#4c000000); }
